import React, { useState } from 'react'

function Login() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)

  // senha fixa (voc pode mudar aqui)
  const FIXED_PASSWORD = 'lbstore123#'

  const handleSubmit = (e) => {
    e.preventDefault()
    setLoading(true)
    setError('')

    setTimeout(() => {
      // verifica apenas a senha (login fake)
      if (password === FIXED_PASSWORD) {
        localStorage.setItem('token', 'fake-jwt-token')
        console.log('Login realizado com sucesso!')
        alert(' Login realizado com sucesso!')
        window.location.href = '/dashboard' // redireciona para o dashboard
      } else {
        setError(' Senha incorreta. Tente novamente.')
      }
      setLoading(false)
    }, 800)
  }

  return (
    <div className='flex min-h-screen items-center justify-center bg-[#491C47] bg-gradient-to-r px-4'>
      <div className='relative w-full max-w-md rounded-xl bg-white/10 p-10 text-left shadow-xl backdrop-blur-xl'>
        <h1 className='mb-6 text-center text-4xl font-extrabold text-white'>Acesse sua Conta</h1>
        <p className='mb-6 text-center text-gray-300'>Bem-vindo(a) de volta! Faa login para continuar.</p>
        {error && <div className='mb-4 text-center text-red-500'>{error}</div>}

        <form onSubmit={handleSubmit} className='space-y-6'>
          <div>
            <label htmlFor='email' className='mb-1 block font-medium text-gray-300'>
              Login
            </label>
            <input
              id='email'
              type='text'
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className='w-full rounded-lg border border-gray-300 bg-transparent px-4 py-3 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500'
              placeholder='Digite seu e-mail ou nome de usurio'
              required
            />
          </div>

          <div>
            <label htmlFor='password' className='mb-1 block font-medium text-gray-300'>
              Senha
            </label>
            <input
              id='password'
              type='password'
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className='w-full rounded-lg border border-gray-300 bg-transparent px-4 py-3 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500'
              placeholder='Digite sua senha'
              required
            />
          </div>

          <button
            type='submit'
            disabled={loading}
            className='w-full rounded-lg bg-[#b0e212] py-3 text-xl font-semibold text-white transition-all duration-300 hover:shadow-lg disabled:opacity-70'
          >
            {loading ? 'Carregando...' : 'Entrar'}
          </button>
        </form>
      </div>
    </div>
  )
}

export default Login
